"use client";

import React, { useState } from "react";

export const UserInfoForm = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");

  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleFormSubmit = async (e: any) => {
    e.preventDefault();
    console.log("username", name, "email", email, "message", message);
    setIsSubmitting(true);
    try {
      // TODO: BE url will come here
      // const res = await('/save')
    } catch (err) {
      console.error(err);
      setIsSubmitting(false);
    } finally {
      setIsSubmitting(false);
      alert("Data save email will be sent shortly");
    }
  };

  return (
    <div>
      <form onSubmit={handleFormSubmit}>
        <div>
          <label className="mr-10"> Full name</label>
          <input
            type="text"
            required
            id="fullName"
            onChange={(e) => setName(e.target.value)}
            className="border rounded rounded-lg mb-3 p-1"
            value={name}
          />
        </div>
        <div>
          <label className="mr-5"> Email Address</label>
          <input
            type="email"
            required
            id="email"
            onChange={(e) => setEmail(e.target.value)}
            className="border rounded rounded-lg mb-3 p-1"
            value={email}
          />
        </div>
        <div>
          <label className="mr-10"> Message</label>
          <input
            type="text"
            required
            id="message"
            onChange={(e) => setMessage(e.target.value)}
            className="border rounded rounded-lg mb-3 p-1"
            value={message}
          />
        </div>
        <div className="flex justify-end">
          <button
            type="submit"
            className="text-white p-2 bg-cyan-500 rounded rounded-lg"
            disabled={isSubmitting}
          >
            Submit
          </button>
        </div>
      </form>
    </div>
  );
};
